<template>
    <div class="myAmend">
       <van-nav-bar
            title="我的资料"
            left-arrow
            @click-left="goBack"
            />
        <!-- 资料 -->
        <div class="boxs">
            <div class="useHeads">
                <span>头像</span>
                <img :src="useObj.userImg"/>
                <van-icon name="arrow"/>
                <van-uploader :after-read="afterRead" />
            </div>
            <van-button type="primary"  @click="show = true" class="useName" >
                <span>用户名</span>
                <span>{{useObj.nickName}}</span>
                <van-icon name="arrow"/>
            </van-button>
             
            <div class="useSex">
                <span>性别</span>
                <span>{{useObj.sex}}</span>
                <van-icon name="arrow"/>
            </div>
            <div class="usePhone">
                <span>绑定手机</span>
                <span>+86 {{useObj.phone | ff_iphon}}</span>
                
            </div>
            <van-button type="primary"  @click="showPass = true" class="useName" >
                <span>修改密码</span>
                <span></span>
                <van-icon name="arrow"/>
            </van-button>
            <div class="useSite" @click="ff_goPages('Location')">
                <span>收货地址</span>
                <van-icon name="arrow"/>
            </div>
        </div>
        
        <van-cell left title="个性化推荐">
                <template #right-icon>
                    <van-switch v-model="checked" size="21" active-color='#6d87c4'/>
                </template>
        </van-cell>

        <div class="deteLogin" @click="ff_outLogin">退出登录</div>
      

      <van-overlay :show="show" @click="show = false">
                <div class="wrapper">
                     <div class="block" @click.stop>
                         <div class="hears">修改用户名<van-icon name="cross" @click.stop="xx"/></div>
                         <div class="bodys">
                             <input v-model="nickName" placeholder="修改名称">
                             <div @click.stop="ff_up">提交</div>
                         </div>
                     </div>
                </div>
      </van-overlay>
       <van-overlay :show="showPass" @click="showPass = false">
                <div class="wrapper">
                     <div class="block" @click.stop>
                         <div class="hears">修改密码<van-icon name="cross" @click.stop="xx01"/></div>
                         <div class="bodys">
                             <input v-model="passwords" placeholder="修改密码">
                             <div @click.stop="ff_upPass">提交</div>
                         </div>
                     </div>
                </div>
      </van-overlay>
    </div>
</template>

<script>
    import '../assets/less/Amend.less'
    import {inspectFrom} from '../assets/js/inspectFrom/inspectFrom'

    export default {
        name:'Amend',
        data() {
            return {
                useObj   :{},
                checked  : true,
                show     : false,
                nickName :  '',
                showPass : false,
                passwords : '',
              
               
            }
        },
        created() {
             this.$toast.loading({
                        message: '加载中...',
                        // 禁止点击背景
                        forbidClick: true,
                        loadingType: 'spinner',
            });

                    // 初始化用户
                    this.ff_initObj();
                   
        },

        methods: {
            goBack(){
                this.$router.go(-1);
            },
             // 跳转模块
            ff_goPages(name,pid=null){
                this.$router.push({name,query:{pid}});
            },
            xx(){
                this.show = false;
            },
            xx01(){
                this.showPass = false;
            },
            // 修改昵称
            ff_up(){
                this.axios({
                    url:'/updateNickName',
                    method:'POST',
                    data:{
                        appkey      : this.appkey,
                        tokenString : this.useId,
                        nickName    : this.nickName
                    }
                })
                .then(res=>{
                    if(res.data.code == "C001"){
                          this.$toast(res.data.msg);
                          this.show = false;
                          this.ff_initObj();
                    }
                })
                .catch(err=>{
                     
                })
            },
             // 初始化用户
            ff_initObj(){
                this.axios({
                        url:'/findAccountInfo',
                        method:'GET',
                        params:{
                            appkey      : this.appkey,
                            tokenString : this.useId,
                        }
                    })
                    .then(res=>{
                         
                        if(res.data.code == "B001"){ 
                            
                            let a = res.data.result[0];
                            a.sex =  '男'; 
                            this.useObj = a;
                            this.nickName = this.useObj.nickName;
                           
                        }
                    })
                    .catch(err=>{
                         
                    })
            },
            // 更换头像
            afterRead(file) {

              let imgTypes        = ['png','jpg','jpeg']
              let imgType         = file.file.type.split('/')[1];
              let size            = file.file.size;
              let serverBase64Img = file.content.replace(/^data:image\/[A-Za-z]+;base64,/,'');

                this.$toast.loading({
                            message: '上传中...',
                            // 禁止点击背景
                            forbidClick: true,
                            loadingType: 'spinner',
                });
              
              if(imgTypes.indexOf(imgType) != -1){
                  if(size <=  524288){
                    this.axios({
                        url:'/updateAvatar',
                        method:'POST',
                        data:{
                                appkey          : this.appkey,
                                tokenString     : this.useId,
                                imgType,
                                serverBase64Img,
                        }
                    })
                    .then(res=>{
                        if(res.data.code = "H001"){
            
                            this.$toast(res.data.msg)
                            this.ff_initObj();
                        }
                    })
                    .catch(err=>{
                   
              })
                      
                  }else{
                      this.$toast('上传失败 图片请小于0.5M')
                  }
                   
              }else{
                  this.$toast('上传失败 图片格式错误')
              }
             
            },
            // 注销登录
            ff_outLogin(){
                    this.$dialog.confirm({
                    message: '确认退出登录',
                    })
                    .then(() => {
                         this.axios({
                            url:'/logout',
                            method:'POST',
                            data:{
                                    appkey          : this.appkey,
                                    tokenString     : this.useId,
                            }
                            })
                            .then(res=>{
                               if(res.data.code == "F001"){
                                         
                                         window.localStorage.removeItem('useId');
                                         this.useId = '';
                                         
                                         this.$toast.loading({
                                            message: '注销中...',
                                            forbidClick: true,
                                            loadingType: 'spinner',
                                         });
                                         setTimeout(()=>{
                                                  this.$router.push({name:'Home'});
                                                  this.useId  = null;
                                                  this.appkey = null;
                                         },400)
                                         
                                         
                               }
                            })
                            .catch(err=>{
                                 
                            })
                            
                    })
                    .catch(()=>{
                         
                    })


                 
            },
            // 修改密码
            ff_upPass(){
                 let obj = {
                    password:{
                        value    :  this.passwords,
                        errorMsg :  '密码格式错误',
                        reg      :   /^[A-Za-z]\w{5,15}$/
                    },
                }
                let isTest = inspectFrom.inspect(obj);
               
                if(isTest){
                        this.axios({
                            url         : '/updatePassword',
                            method      : 'POST',
                            data:{
                                appkey      : this.appkey,
                                tokenString : this.useId,
                                oldPassword : this.passWord,
                                password    : this.passwords,

                            }
                            

                        })
                        .then(res=>{  
                            if(res.data.code == "E001"){
                                       this.$toast(res.data.msg);
                                       this.showPass = false;
                                       window.sessionStorage.removeItem('passWord');
                                       window.localStorage.removeItem('useId');
                                       setTimeout(()=>{
                                           this.$router.push({name : 'Login'});
                                           this.$toast('请重新登录');

                                       },300)
                                       

                            }else{
                                   this.$toast(res.data.msg);
                            }
                           
                        })
                        .catch(err=>{
                            
                        })

                } 
            },
         

                    
        },
        filters:{
            ff_iphon(val){
                if(val){
                    let strK = val.substr(0,3);
                    let strW = val.substr(7,4);
                    let sthPhone = `${strK}****${strW}`;
                    return sthPhone;

                }
                
            },
            
        }
    }
</script>

<style lang="less" scoped>

</style>